const Accordion = () => {
  const location = useLocation(); // 
  const [expanded, setExpanded] = React.useState<string | false>(false);

  const handleChange =
    (panel: string, title: string) =>
    (event: React.SyntheticEvent, isExpanded: boolean) => {
      // title  URL  
      window.location.hash = "";

      // 
      setExpanded(isExpanded ? panel : false);
    };

  return (
    <>
      {resumeData.map((data, index) => (
        <MuiAccordion
          TransitionProps={{ unmountOnExit: true }}
          key={index}
          expanded={
            decodeURI(location.hash) === `#${data.title}` ||
            expanded === `panel ${index}`
          }
          id={data.title}
          className={data.feat ? "feat" : ""}
          onChange={handleChange(`panel ${index}`, data.title)}
        >
          {/* <Link underline="none" href={`#${data.title}`}> */}
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
            sx={{ bgcolor: "primary.bgLight" }}
          >
            <Grid container direction="row" spacing={1}>
              <Grid container justifyContent="space-between">
                <Grid item>
                  {data.jobs.map((job, index) => (
                    <Typography
                      variant="body2"
                      color="primary.fontColorDark"
                      key={index}
                      component="span"
                      pr={3}
                      pl={1}
                    >
                      {job}
                    </Typography>
                  ))}
                </Grid>
                <Grid item>
                  <Typography variant="body2" color="primary.fontColorDark">
                    {data.date}
                  </Typography>
                </Grid>
              </Grid>
              <Grid container spacing={1} p={2}>
                <Stack direction="row" spacing={1}>
                  <Grid container justifyContent="center" spacing={1}>
                    {data.skills.map((skill, index) => (
                      <Grid item key={index}>
                        <Chip label={skill} color="primary" size="small" />
                      </Grid>
                    ))}
                  </Grid>
                </Stack>
              </Grid>

              <Grid container justifyContent="left">
                <Typography variant="h6" pl={2} color="primary.fontColorDark">
                  {data.title}
                </Typography>
              </Grid>
            </Grid>
          </AccordionSummary>
          {/* </Link> */}
          <AccordionDetails>
            <Grid container alignItems="center">
              <Grid item xs={9} p={1}>
                <Typography
                  color="primary.fontColorDark"
                  fontWeight="bold"
                  pt={1}
                >
                  
                </Typography>
                <Typography color="primary.fontColorDark">
                  {data.contents}
                </Typography>
                <Typography
                  color="primary.fontColorDark"
                  fontWeight="bold"
                  pt={1}
                >
                  
                </Typography>
                <Typography color="primary.fontColorDark" className="thoughts">
                  {data.thoughts}
                </Typography>
                <Typography
                  color="primary.fontColorDark"
                  fontWeight="bold"
                  pt={1}
                >
                  <Link
                    href={data.link}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    {data.link}
                  </Link>
                </Typography>
              </Grid>
              <Grid item xs={3} justifyContent="center" p={2}>
                <Stack direction="row" spacing={1}>
                  <Grid container justifyContent="center" spacing={1}>
                    {data.assign.map((assign, index) => (
                      <Grid item key={index}>
                        <Chip label={assign} color="primary" size="small" />
                      </Grid>
                    ))}
                  </Grid>
                </Stack>
                <Stack
                  direction="row"
                  spacing={1}
                  justifyContent="center"
                  p={2}
                >
                  <Chip label={data.scale} color="success" size="small" />
                </Stack>
              </Grid>
            </Grid>
          </AccordionDetails>
        </MuiAccordion>
      ))}
    </>
  );
};
export default Accordion;
